.at-blk-shp > :not(.at-shp):not(.at-z-idx) {
  --at-z-idx: 2;
  --at-pos: relative;
  z-index: var(--at-z-idx, initial);
  position: var(--at-pos, initial);
}
.at-shp {
  --at-pos: absolute;
  --at-w: 100%;
